<template>
  <main class="login-index">
    <header>
      <img src="../assets/images/jiahua_logo.png" alt="">
    </header>
    <article class="inp-container">
      <div class="block">
        <span class="icon icon-01"></span>
        <input type="text" placeholder="输入用户名" v-model="loginName">
      </div>
      <div class="block">
        <span class="icon icon-02"></span>
        <input type="password" placeholder="输入密码" v-model="password">
      </div>
      <div class="block">
        <span class="icon icon-03"></span>
        <input class="code-inp" type="number" placeholder="输入图形中数字" v-model="inpCode">
        <div class="auth-code">
          <canvas id="canvas" @click="drawPic"></canvas>
        </div>
      </div>
    </article>
    <div class="login-btn" @click="loginFn">立即登录</div>
  </main>
</template>
<script>
  import { Toast } from 'mint-ui';
  export default {
    data() {
      return {
        loginName: '',
        password: '',
        inpCode:'',//用户输入的验证码
        authCode:'', //随机图形验证码
      }
    },
    created(){

    },
    mounted(){
      this.drawPic();
    },
    methods:{
      /**
       * 登录函数
       * loginName：用户名
       * password：密码
       */
      loginFn() {
        console.log('验证码',this.authCode);
        if(this.loginName.trim() == ''){
          Toast('请输入用户名');
          return;
        }else if(this.password.trim() == ''){
          Toast('请输入密码');
          return;
        }else if(this.inpCode != this.authCode){
          Toast('验证码错误')
          return;
        }
        this.$request.post("Login/login", {
          loginName: this.loginName,
          password: this.password
        }, res => {
          if(res.code == 200){
            //登录成功
            this.setCookie('username', this.loginName, 1)
            this.$router.push('/');
          }else if(res.code == -16004){
            //用户名不存在
            Toast('用户名不存在')
          }else if(res.code == -16008){
            //密码错误
            Toast('密码错误')
          }else{
            Toast(res.msg);
          }
        })
      },
      /**
       * 生成一个随机数
       */
      randomNum(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
      },
      /*
      * 生成一个随机色
      * */
      randomColor(min, max) {
        var r = this.randomNum(min, max);
        var g = this.randomNum(min, max);
        var b = this.randomNum(min, max);
        return `rgb(${r},${g},${b})`;
      },
      /*
      *绘制验证码图片
      * */
      drawPic() {
        this.authCode = '';
        this.errAuthCode = false;
        var canvas = document.getElementById("canvas");
        var width = canvas.width;
        var height = canvas.height;
        var ctx = canvas.getContext('2d');
        ctx.textBaseline = 'bottom';
        /**绘制背景色**/
        ctx.fillStyle = this.randomColor(180, 230); //颜色若太深可能导致看不清
        ctx.fillRect(0, 0, width, height);
        /**绘制文字**/
        var str = '0123456789';
        for (var i = 0; i < 4; i++) {
          var txt = str[this.randomNum(0, str.length)];//随机的字
          ctx.fillStyle = this.randomColor(50, 160);  //随机生成字体颜色
          ctx.font = this.randomNum(18, 40)*4 + 'px SimHei'; //随机生成字体大小
          var x = 20 + i * 20*3;
          var y = this.randomNum(25, 35)*4;
          var deg = this.randomNum(-30, 30);//字体的旋转角度
          //修改坐标原点和旋转角度
          ctx.translate(x, y);
          ctx.rotate(deg * Math.PI / 180);
          ctx.fillText(txt, 0, 0);
          //恢复坐标原点和旋转角度
          ctx.rotate(-deg * Math.PI / 180);
          ctx.translate(-x, -y);
          this.authCode += txt;
        }
        /**绘制干扰线**/
        for (var i = 0; i < 5; i++) {
          ctx.strokeStyle = this.randomColor(180,230);
          ctx.beginPath();
          ctx.moveTo(this.randomNum(0, width), this.randomNum(0, height));
          ctx.lineTo(this.randomNum(0, width), this.randomNum(0, height));
          ctx.stroke();
        }
        /**绘制干扰点**/
        for (var i = 0; i < 40; i++) {
          ctx.fillStyle = this.randomColor(150,200);
          ctx.beginPath();
          ctx.arc(this.randomNum(0, width), this.randomNum(0, height), 1, 0, 2 * Math.PI);
          ctx.fill();
        }
      }
    }
  }
</script>
<style lang="scss">
  $mainColor:#ff5e2d;
  .login-index{
    padding: 0 .72rem;
    height: 100%;
    overflow: hidden;
    background: #fff;
    header{
      margin-top: 2.4rem;
      margin-bottom: .87rem;
      text-align: center;
      img{
        width: 3.6rem;
      }
    }
    .inp-container{
      .block{
        border-bottom: 1px solid #f2f2f2;
        width: 100%;
        height: 1.12rem;
        box-sizing: border-box;
        .icon{
          width: .34rem;
          height: .39rem;
          background: #eeeeee;
          display: inline-block;
          vertical-align: middle;
          margin-right: .49rem;
          margin-left: .22rem;
          &.icon-01{
            background: url("../assets/images/user_icon.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
          }
          &.icon-02{
            background: url("../assets/images/password_icon.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
          }
          &.icon-03{
            background: url("../assets/images/securitycode_icon.png") no-repeat;
            -webkit-background-size: 100% 100%;
            background-size: 100% 100%;
          }
        }
        input{
          line-height: 1.1rem;
          height: 1.1rem;
          color: #353535;
          font-size: .28rem;
          vertical-align: middle;
          &.code-inp{
            width: 2.6rem;
          }
        }
        input::-webkit-input-placeholder{
          color: #888888;
        }
        .auth-code{
          width: 1.24rem;
          height: .65rem;
          background: #eeeeee;
          float: right;
          margin-top:.22rem;
        }
        #canvas{
          width: 1.24rem;
          height: .65rem;
        }
      }
    }
    .login-btn{
      width: 4.4rem;
      height: .88rem;
      line-height: .88rem;
      background: $mainColor;
      color: #fff;
      font-size: .36rem;
      text-align: center;
      margin: 0 auto;
      margin-top: .64rem;
      border-radius: 44px;
    }
  }
</style>



