
//设置cookie
function setCookie(cname, cvalue, exdays) {
  var d = new Date();
  d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
  var expires = "expires=" + d.toUTCString();
  document.cookie = cname + "=" + cvalue + "; " + expires;
  console.info(document.cookie);
}
//获取cookie
function getCookie (cname) {
  var name = cname + "=";
  var ca = document.cookie.split(";");
  for (var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) == " ") c = c.substring(1);
      if (c.indexOf(name) != -1) return c.substring(name.length, c.length);
  }
  return "";
}

var MyPlugin = {
  // 自定义插件
  install(Vue, options) {
      Vue.prototype.setCookie = setCookie;
      Vue.prototype.getCookie = getCookie;
      Vue.prototype.initRem = () => {
        new function() {
            var _self = this;
            _self.width = 750; //设置默认最大宽度
            _self.fontSize = 100; //默认字体大小
            _self.changePage = function() {
                var width = document.documentElement.getBoundingClientRect().width ;
                if(415 > document.documentElement.getBoundingClientRect().width >375){
                    width = 375;
                }
                // var width = document.documentElement.getBoundingClientRect().width
                // > 375 ? 375 : document.documentElement.getBoundingClientRect().width;
                var p = width / _self.width;
                if (p <= 1) {
                    document
                        .getElementsByTagName("html")[0]
                        .setAttribute("style", "font-size:" + p * _self.fontSize + "px !important");
                } else {
                    document
                        .getElementsByTagName("html")[0]
                        .setAttribute("style", "font-size:" + 1 * _self.fontSize + "px !important");
                }
            };
            _self.changePage();
            window.addEventListener("resize", function() {
                _self.changePage();

            }, false);
        };
    }

  }
}
export default MyPlugin



