<template>
<main class="index">
  <header>
      <div class="video-play">
        <!-- 直播区域 -->
        <template v-if="push_status == 1">
          <video-player  class="video-player vjs-custom-skin"
                     ref="videoPlayer"
                     webkit-playsinline="true"
                     :options="playerOptions"
                      v-show="push_status == 1"
           > </video-player>
      </template>
      <template v-else>
        <img :src="live_cover" alt="" v-show="push_status == 2">
      </template>


      </div>
  </header>
  <section class="article">
       <nav>
        <span @click="selected = 1" :class="{active:selected == 1}">互动</span>
        <span @click="selected = 2" :class="{active:selected == 2}">免责</span>
        <span @click="selected = 3" :class="{active:selected == 3}">课程表</span>
       </nav>
      <!-- 互动 -->
      <div class="comment-box" v-show="selected == 1">
          <div class="comment " id="sidebar" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
            <mt-loadmore :top-method="loadTop"  @top-status-change="handleTopChange"  topPullText="请点击历史记录页查看更多内容" :auto-fill="false" ref="loadmore" >

            <div class="comment-item" v-for="item in comments" >
              <img :src="item.avatar" alt="">
              <div>
                <h5><span class="name">{{item.name}}</span><span class="type ass" v-if="item.type==3"></span><span class="type tea" v-if="item.type==2"></span><span class="time">{{item.time}}</span></h5>
                <p>
                   <template v-if="item.emojitype == 1">
                      <img :src="item.emojiexpression" alt="">
                  </template>
                  <template v-else>
                      {{item.content}}
                  </template>

                </p>
              </div>
            </div>
            <span class="txt"></span>

            <div slot="top" class="mint-loadmore-top">
              <span v-show="topStatus !== 'loading'" :class="{ 'is-rotate': topStatus === 'drop' }">↓</span>
              <span v-show="topStatus === 'loading'">
                <mt-spinner type="snake"></mt-spinner>
              </span>
            </div>

          </mt-loadmore>
          </div>
           <!-- <div class="footer">
             <input type="text" placeholder="快来和老师聊聊吧~~" v-model="commentText" @keyup.enter="sendComment">
             <span id="sendBtn" @click="sendComment"></span>
          </div> -->

      </div>
      <!-- 免责 -->
       <div  v-if="selected == 2" class="disclaimer">
         <img :src="disclaimer" alt="">
       </div>
      <!-- 课程表 -->
      <div  v-if="selected == 3">
        <img :src="courseBg" alt="">
      </div>


  </section>
</main>
</template>
<script>
require("../assets/js/live/web_socket.js");
import { Loadmore } from "mint-ui";
import { Toast } from "mint-ui";
import { videoPlayer } from "vue-video-player";
import socketClient from "../assets/js/live/socketClient.js";
import wxfun from "../assets/js/live/wxfunction.js";
import "videojs-contrib-hls";
require("video.js/dist/video-js.css");
require("vue-video-player/src/custom-theme.css");
export default {
  data() {
    return {
      userId: "", //用户id
      EmojiData:[],//表情包数据
      courseId: 1000010029, //当前房间id
      commentText: "", //留言内容
      courseBg: "", //课程表
      disclaimer: "", //免责声明
      push_status: 2, //推流专题1正则推流，2没有推流
      comments: [], //互动区消息
      live_cover: "", //直播间封面
      pull_url: "", //直播地址
      commentLastId: 0, //最新一个评论id
      maxL: "",
      minL: "", //上一次加载最后一个msgid
      commentFirst: true, //互动区第一次加载
      allLoaded: false, //3017第一次加载可以滚动到最新，后则不行
      topStatus: "",
      selected: 1, //tab切换
      playerOptions: {}, //课程直播参数
      wrapperHeight: "", //互动区高度
      metaInfo: {
        //seo
        title: "家华直播间",
        name: "家华直播间",
        content: ""
      }
    };
  },
  name: "async",
  metaInfo() {
    return {
      title: this.metaInfo.title,
      meta: [
        {
          name: this.metaInfo.name, //keyWords
          content: this.metaInfo.content
        }
      ],
      link: [
        {
          // set link
          rel: "",
          href: "document.location.hostname"
        }
      ]
    };
  },
  components: {
    videoPlayer,
    Loadmore
  },
  created() {
    this.getData();
  },
  beforeDestroy() {
    wxfun.leaveRoom(+this.userId, +this.courseId);
    clearInterval(socketClient.setping);
    socketClient.ws.close();
  },
  mounted() {
    this.$nextTick(() => {
      window.scroll(0, 0);
    });
  },

  methods: {
    handleTopChange(status) {
      this.topStatus = status;
    },
    /*
    * 获取表情包
    * */
    getEmoji() {
     this.$request.get('/Expression/getExpressionList','',res => {
                    if(res.code == 200){
                        this.EmojiData =res.data;
                        console.log(res.data)
                         this.getHistory(0, false, 20, true);
                    }
                });
    },
    loadTop() {
      // 加载更多数据
      console.log(123455);
      if (!this.allLoaded) {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();
          this.getHistory(this.minL, false, 20, true); //向上滚动加载更多
        }, 1000);
      } else {
        this.$refs.loadmore.onTopLoaded();
        Toast({
          message: "亲,到顶了哦",
          duration: 2000
        });
      }
    },

    /*
    * 获取历史消息
    * */
    getHistory(msgId, isReadAll, count, isForward) {
      wxfun.getMsgHis(
        +this.userId,
        +this.courseId,
        msgId,
        isForward, //顺序还是倒叙
        isReadAll, //分页还是不分页
        count //条数
      );
    },
    /**
     发送评论
      * */
    sendComment(e) {
      // e.target.previousElementSibling.blur();
      if (this.commentText.trim() == "" || this.commentText.length == "") {
        this.commentErr = true;
        Toast({
          message: "评论为空哦",
          duration: 1000
        });
      } else {
        //发表评论
        if (socketClient.ws.readyState == 3) {
          Toast({
            message: "网络超时，请刷新",
            duration: 1000
          });
        }
        wxfun.sendMessage(
          this.userId,
          this.courseId,
          this.commentText.replace(/[\r\n]/g, "")
        );
      }

      this.commentText = "";
    },
    /**
     时间日期格式
      * */
    formatDate(obj) {
      function f(num) {
        return num > 9 ? num + "" : "0" + num;
      }

      return (
        f(obj.getMonth() + 1) +
        "-" +
        f(obj.getDate()) +
        " " +
        f(obj.getHours()) +
        ":" +
        f(obj.getMinutes())
      );
    },
    /**
     websocket
      数据
      * */
    onmessage(msg) {
      const data = msg.data;
      console.log(msg);
      // alert(msg.subcmd);
      switch (msg.subcmd) {
        // case 2001://登录房间失败
        //     if (data.errInfo.errID == 1025) {
        //         wxfun.leaveRoom(+this.userId, +this.$route.params.courseId)
        //         this.loginSuccess = false
        //     }
        //     break
        case 2002: //登录房间成功
          console.log(data);
          // alert("登录房间成功");
          window.onbeforeunload = () => {
            wxfun.leaveRoom(+this.userId, +this.courseId);
          };
          this.getEmoji();

          break;
        case 2181: //进入房间人数统计
          break;
        case 2226: //直播通知
          this.push_status = 1;
          break;
        case 2227: //直播结束通知
          this.push_status = 2;
          break;
        case 2218: //结束直播成功
          break;
        case 3011:
          Toast({
            message: "发表评论成功",
            iconClass: "icon icon-success",
            duration: 1000
          });
          break;
        case 3012:
         if (data.msg.content.indexOf('[-') >= 0) {
            this.isEmojiBox = false;

              var emojitype = 1;
               for (var j = 0; j < this.EmojiData.length; j++) {
                  if (data.msg.content.indexOf(this.EmojiData[j].expression_number) >= 0) {
                    var emojiexpression = this.EmojiData[j].qiniuImg;
                    console.log(emojiexpression)
                  }
                }
            }
          this.comments.push({
            name: data.msg.srcUser.alias,
            avatar: data.msg.srcUser.head, // || "/images/default/userDefault.png",
            content: data.msg.content,
             type:data.msg.srcUser.UserType,//1：学生;2:老师;3：助教
            time: this.formatDate(new Date(data.msg.msgTime * 1000)),
             emojitype: emojitype || 0,
            emojiexpression: emojiexpression || 0
          });
          setTimeout(() => {
            //滚动到底部
            document.getElementsByClassName(
              "comment"
            )[0].scrollTop = document.getElementsByClassName(
              "txt"
            )[0].offsetTop;
          }, 500);
          break;
        case 3017: //获取聊天室消息成功
          if (data.msgs) {
            this.maxL = data.msgs[0].msgId;
            this.minL = data.msgs[data.msgs.length - 1].msgId;
            if (data.msgs.length < 20) {
              this.allLoaded = true;
            }
            for (var i = 0, len = data.msgs.length; i < len; i++) {
              if (data.msgs[i].content.indexOf('[-') >= 0) {
                    var emojitype = 1;
                    for (var j = 0; j < this.EmojiData.length; j++) {
                        if (data.msgs[i].content.indexOf(this.EmojiData[j].expression_number) > -1) {
                          var emojiexpression = this.EmojiData[j].qiniuImg;
                          this.comments.unshift({
                          name: data.msgs[i].srcUser.alias,
                          type:data.msgs[i].srcUser.UserType,
                          avatar: data.msgs[i].srcUser.head, // || "/images/default/userDefault.png",
                          content: data.msgs[i].content,
                          time: this.formatDate(new Date(data.msgs[i].msgTime * 1000)),
                          emojitype: emojitype  ,
                          emojiexpression: emojiexpression
                        });
                        }
                      }
               }else{
                 this.comments.unshift({
                    name: data.msgs[i].srcUser.alias,
                    avatar: data.msgs[i].srcUser.head, // || "/images/default/userDefault.png",
                    content: data.msgs[i].content,
                    type:data.msgs[i].srcUser.UserType,
                    time: this.formatDate(new Date(data.msgs[i].msgTime * 1000)),
                    emojitype: 0 ,
                    emojiexpression: 0
                  });
               }
            }

            if (this.commentFirst == true) {
              this.$nextTick(() => {
                setTimeout(() => {
                  this.wrapperHeight =
                    document.documentElement.clientHeight -
                    this.$refs.wrapper.getBoundingClientRect().top;
                });
              });
              setTimeout(() => {
                //滚动到底部
                document.getElementsByClassName(
                  "comment"
                )[0].scrollTop = document.getElementsByClassName(
                  "txt"
                )[0].offsetTop;
              }, 500);
              this.commentFirst = false;
            }
          } else {
            this.allLoaded = true;
            this.commentFirst = false;
          }
          break;
      }
    },
    /**
     链接ws
      * */
    connect(host, port) {
      socketClient.connect(
        this.userId,
        "",
        this.courseId,
        this.onmessage,
        () => {},
        host,
        port
      );
    },
    /**
     * 获取banner跟直播间信息
     */
    getData() {
      this.$request.get("/BannerAndLive/fetchBannerAndLiveData", "", res => {
        console.log(res);
        if (res.code == 200) {
          this.userId = this.getCookie("username");
          this.connect(res.data.webSocket.host, res.data.webSocket.port);
          this.pull_url = res.data.pull_url;
          //封面
          this.live_cover = res.data.live_cover;
          this.push_status = res.data.push_status;
          //课程表
          this.courseBg = res.data.course_table;
          //免责声明
          this.disclaimer = res.data.disclaimer;
          this.getPullVideo();
        }
      });
    },
    /**
     * 播放
     */
    getPullVideo() {
      var pull_url;
      var pull_url;
      if (this.push_status == 1) {
        pull_url = this.pull_url;
      } else {
        pull_url = "///";
      }
      if (this.push_status == 1) {
        this.$nextTick(() => {
          this.playerOptions = {
            // height: "100%",
            // width:'100vw',
            fluid: true,
            sources: [
              {
                withCredentials: false,
                type: "application/x-mpegURL",
                src: this.pull_url //"http://live.hkstv.hk.lxdns.com/live/hks/playlist.m3u8" //
              }
            ],
            controlBar: {
              timeDivider: false,
              durationDisplay: false
            },
            flash: { hls: { withCredentials: false } },
            html5: { hls: { withCredentials: false } },
            poster: this.live_cover,
            playsinline: true
          };
        });
      } else {
        pull_url = "///";
      }
    }
  },

  watch: {
    commentText(val) {
      if (val.trim() != "") {
        document.getElementById("sendBtn").className = "active";
      } else {
        document.getElementById("sendBtn").className = "";
      }
    }
  }
};
</script>

<style lang="scss">
$borderc: #ebebeb;
main {
  margin: 0 auto;
  overflow: hidden;
}
.icon-success {
  display: block;
  background: url("../assets/images/icon-success.png") center no-repeat;
  width: 1rem;
  height: 1rem;
  background-size: 100%;
  margin: 0 auto;
}
.article {
  margin: 0 auto;
  overflow: hidden;
  nav {
    height: 1rem /* 100/100 */;
    font-size: 0.32rem /* 32/100 */;
    display: flex;
    border-bottom: 1px solid $borderc;
    span {
      display: inline-block;
      height: 1rem;
      text-align: center;
      flex: 1;
      line-height: 1rem;
      font-size: 0.32rem /* 32/100 */;
      position: relative;
      & + span {
        border-left: 1px solid $borderc;
      }
      &.active {
        color: #ff5e2d;
      }
      &.active:before {
        content: "";
        display: block;
        position: absolute;
        bottom: 0.16rem /* 16/100 */;
        height: 2px;
        border-radius: 1px;
        width: 30%;
        left: 50%;
        transform: translateX(-50%);
        background: #ff5e2d;
      }
    }
  }
  .disclaimer {
    img {
      display: block;
      width: 100vw;
      height: auto;
    }
  }
  // 评论区

  .comment {
    background: #f5f7f8;
    padding: 0 0.24rem;
    overflow-y: scroll;
    // padding-bottom: 1.1rem;
    overflow-x: hidden;
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
  }
  .footer {
    height: 1.1rem;
    width: 100%;
    position: fixed;
    bottom: 0px;
    cursor: pointer;

    padding: 0.15rem 0;
    border-top: 1px solid $borderc;

    display: flex;
    justify-content: space-between;
    align-content: center;
    background-color: #fff;
    z-index: 11;
    input {
      border: 1px solid #d2d2d2;
      width: 100%;
      margin-left: 0.24rem;
      padding-left: 0.26rem;
      font-size: 0.3rem;
      color: #888;
      border-radius: 0.4rem;
    }
    #sendBtn {
      width: 0.8rem;
      height: 0.8rem;
      flex-shrink: 0;
      display: inline-block;
      background: url("../assets/images/send_button_normal.png") center center
        no-repeat;
      background-size: 0.8rem auto;
      margin: 0 0.24rem;
      &.active {
        background: url("../assets/images/send_button_press.png") center center
          no-repeat;
        background-size: 0.8rem auto;
      }
    }
  }
  .comment-item {
    display: flex;
    padding-top: 0.24rem /* 24/100 */;
    margin: 0;
    // justify-content: space-around;
    h5 {
      height: 0.8rem /* 80/100 */;
      line-height: 0.8rem;
    }

    .name {
      color: #888;
      margin-right: 0.1rem;
    }
    .time {
      color: #b2b2b2;
    }
    .type{
      display: inline-block;
      width:.56rem /* 56/100 */;
      height: .32rem /* 32/100 */;
      vertical-align: middle;
    margin-right: .16rem /* 16/100 */;
    border-radius: .08rem /* 10/100 */;
    }
    .ass{
      background: url('../assets/images/assistant_label.png') center center no-repeat;
      background-size: cover;
    }
    .tea{
      background: url('../assets/images/teacher_label.png') center center no-repeat;
      background-size: cover;
    }
    &>img {
      width: 0.8rem;
      margin-right: 0.1rem;
      height: 0.8rem;
      flex-shrink: 0;
      border-radius: 50%;
    }
    p {
      padding: 0.28rem /* 26/100 */;
      background: #fff;
      border-radius: 0.4rem;
      color: #353535;
      line-height: 1.5;
      img{
        width: 1.4rem;
        height: 1.08rem;
      }
    }
  }
}
</style>




