import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index'
import login from '@/page/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      meta: {
        title: '家华直播'
      }
    },
    {
      path: "*",
      redirect: "/"
    }
    // {
    //   path:'/login',
    //   name:'login',
    //   component:login,
    //   meta: {
    //     title: '家华登录'
    //   }
    // }
  ]
})



