<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  name: "App",

  created() {
    this.initRem();
   this.IsPC();
    this.getUreId();
  },
  methods: {
    /*
    * 检测是否登录
    * */
    checkLogin() {
      this.$request.get("/Login/checkLogin", "", res => {
        if (res.code == 200) {
          //已登录
          this.$router.push("/");
        } else if (res.code == -5002) {
          //未登录
          this.$router.push("/login");
        }
      });
    },
    getUreId(){
      this.$request.get("/Login/autoLogin", "", res => {
        if (res.code == 200) {
          //已登录
          this.setCookie('username', res.data.userId, 1)
        } else{
          //未登录
          Toast({
              message: '网络超时，请刷新',
              duration: 1000
          });
        }
      });
    },
    IsPC() {
      var href = window.location.href;
      var ua = navigator.userAgent;
      var ipad = ua.match(/(iPad).*OS\s([\d_]+)/),
        isIphone = !ipad && ua.match(/(iPhone\sOS)\s([\d_]+)/),
        isAndroid = ua.match(/(Android)\s+([\d.]+)/),
        isMobile = isIphone || isAndroid || ipad;

      if (isMobile) {
        if (href.indexOf("H5") < 0) {
          window.location.href =
            "http://" + document.location.hostname + "/H5.html";
        }
      } else {
        if (href.indexOf("H5") > 0) {
          window.location.href = "http://" + document.location.hostname;
        }
      }
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  overflow: hidden;
  /* margin-top: 60px; */
}
</style>



